.room .messenger {
    background-color: rgba(87, 66, 33, 0.103);
    box-shadow: inset 0 0 5px rgba(128, 106, 66, 0.836), 0 0 18px #ff650040;
    border-radius: 5px;
    overflow-y: auto;
    color: wheat;
    height: 47vh;
