.message-form {
    margin: 0 0;
    padding: 5px 2px;
    background-color: rgba(240, 248, 255, 0.466);
    border: solid 2px wheat;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    border-top-left-radius: 0px;
    border-top-right-radius: 0px;
  }
  
.message-form .send-input {
    background-color: rgba(240, 248, 255, 0);
    border: none !important;
    width: 89% !important;
    color: rgb(167, 69, 30);
}

.message-form .send-input::-webkit-input-placeholder {
    color: white;
}

.message-form .send-input:-ms-input-placeholder {
    color: white;
}

.message-form .send-input::placeholder {
    color: white;
}

.message-form button {
    margin: 3px 5px 0px 0px;
}

.message-form button i {
    padding: 9px 1px;
    -webkit-transform: translate(-2px, 0px);
            transform: translate(-2px, 0px);
    text-shadow: 0 0 3px rgb(41, 41, 41);
}

.messenger .your-message {
    text-align: end;
    background-color: rgb(167, 69, 30);
    color: white;
    margin-right: 20px;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 0px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    list-style: none;
    -webkit-animation-name: grow;
            animation-name: grow;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
  
@-webkit-keyframes grow {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
  
@keyframes grow {
    0% {
        -webkit-transform: scale(0);
                transform: scale(0);
    }

    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}
  
.messenger .others-message {
    text-align: start;
    background-color: white;
    color: rgb(199, 50, 6);
    margin-left: 20px;
    font-family: 'Roboto';
    font-weight: 500;
    border-bottom-left-radius: 0px;
    border-bottom-right-radius: 8px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    list-style: none;
    -webkit-animation-name: grow;
            animation-name: grow;
    -webkit-animation-duration: 0.4s;
            animation-duration: 0.4s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.others-message span {
