.goodWord {
    transition: .2s;
    background: linear-gradient(to top right, #144253, #2b8d22);
    color: white;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin: 0 auto;
    border: none;
    font-size: 50px;
}

.passWord {
    transition: .2s;
    background: linear-gradient(to top right, #3a0038, #dc0808);
    color: white;
    width: 75px;
    height: 75px;
    border-radius: 50%;
    margin: 0 auto;
    border: none;
    font-size: 25px;
}

.goodWord:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.passWord:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.goodWord:active {
    font-size: 80px;
    -webkit-transform: translateY(3px) scale(.5);
            transform: translateY(3px) scale(.5);
}

.passWord:active {
    font-size: 40px;
    -webkit-transform: translateY(3px) scale(.5);
            transform: translateY(3px) scale(.5);
}

.word {
    transition: all .4s;
    width: 185px;
    height: 185px;
    margin: 0 auto;
    background: linear-gradient(200deg,#8008b1,#ff5961);;
    border-radius: 50%;
    box-shadow: 0 0 20px #231f1f48;
