            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-timing-function: inherit;
            animation-timing-function: inherit;
    font-size: 40px;
    right: calc(50% - (185px / 2));
    top: 0;
}

